/* DM Sans font classes */
.dm-sans {
  font-family: "DM Sans", sans-serif;
  font-optical-sizing: auto;
}

.dm-sans-light {
  font-family: "DM Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 300;
}

.dm-sans-normal {
  font-family: "DM Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
}

.dm-sans-medium {
  font-family: "DM Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
}

.dm-sans-semibold {
  font-family: "DM Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
}

.dm-sans-bold {
  font-family: "DM Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* Bubble glassmorphism with liquid glass effect */
.bubble {
  position: relative;
  display: inline-block;
  width: fit-content;
  max-width: 100%;
  margin: 0 auto;
  padding: 1rem;
  border-radius: 200px;
  background: rgba(255, 255, 255, 0.15);
  
  /* Rimuovi il filtro SVG - non funziona su Safari mobile */
  backdrop-filter: blur(12px) saturate(180%);
  -webkit-backdrop-filter: blur(12px) saturate(180%);
  
  border: 1px solid rgba(255, 255, 255, 0.8);
  color: white;
  text-align: left;
  z-index: 1;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15),
              inset 0 2px 10px rgba(255, 255, 255, 0.4);
  overflow: hidden;
  
  /* FIX SAFARI MOBILE - forza rendering GPU */
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  will-change: transform;
}

/* Rimuovi il blocco @supports - non serve pi√π */

.bubble-button {
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 24px;
  padding: 0.75rem 2rem;
  color: white;
  font-size: 1.1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-block;
  
  /* FIX anche per il button */
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.bubble-button:hover {
  background: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-2px) translateZ(0);
  -webkit-transform: translateY(-2px) translateZ(0);
}
